<template>
  <div class="Partner">
    <i class="el-icon-back allback" @click="$emit('back')"></i>
    <canvas id="tree"></canvas>
    <canvas id="flower"></canvas>
    <div class="leftgongao">
      <div>
        <span style="color: skyblue;">{{aqda.man}}</span>
        <b style="padding:0 5px;font-weight: bold;color:red;">❤</b>
        <span style="color: pink;">{{aqda.woman}}</span>
        <audio id="audio" loop controls="controls" autoplay="autoplay">
          <source src="/i/song.ogg" type="audio/ogg" />
          <source :src="'http://192.168.0.104:7788/upload_files/2020-2-17/qyy.mp3' | imgFilter" type="audio/mpeg" />
        </audio>
      </div>
      <div>
        <span>我们恋爱了:</span>
        <div>
          <span>{{aqda.starTime | daytotal}}</span>
          <span>个日夜</span>
        </div>
      </div>
    </div>
    <div class="rightimg">
      <img @click="$emit('clickimg',aqda.img)" :src="aqda.img | imgFilter" alt />
    </div>
    <div :class="zIndex == -1 ? 'zimu' : 'zimunone'">
      欢迎来到我港湾
      <p>聆听我们的故事</p>
    </div>
    <div  v-for="(item,i) in ziArr" :key="i" :class="zIndex == i ? 'zimu' : 'zimunone'">
      {{item}}
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  computed: {
    ...mapState([
      "allnavs",
    ]),
  },
  data() {
    return {
      aqda: {
        img: "http://192.168.0.104:7788/upload_files/2020-2-17/qinglu.jpg",
        man: "舒榆衡",
        woman: "张冰雪",
        starTime: "2018/10/26 22:00:00"
      },
      // 动画
      animationId: "",
      // 字幕数字
      ziArr:[
        "在18年的9月1日，我们进入了同一所大学",
        "在18年的10月1日，我们相约而游，并相识",
        "期间有过很多的故事~ 她成为了我的小老弟",
        "在18年的10月26日,我们闹矛盾了",
        "有趣的故事发生了 ... ...",
        "不知为何我们哭了",
        "可能是命运的驱使吧~",
        "平常不搭理异性的我，跟她表白了",
        "从此挂上这颗歪脖子树无法自拔",
        "爱情的开始总是甜蜜的，我们有说不完的话",
        "但爱情也有苦涩，有时吵到要分道扬镳",
        "慢慢习惯了分分合合的我，也乐在其中了",
        "从此走上了厚脸皮的生活",
        "爱情和她教会了我很多",
        "从屌丝成为她的小可爱、小奶狗、和臭流氓",
        "我不知道要几步",
        "可能只有她才知道其中的苦涩吧~",
        "在20年的2月22日，我构建了这款程序",
        "她生气了~，当时正值疫情放假期间",
        "我又沉浸了代码的世界没有理她",
        "可能这就是直男吧！ 我想给她一个惊喜",
        "一个属于'程序猿'的浪漫",
        "我爱你 傻猪头O(∩_∩)O",
        "舒榆衡这辈子定下你了~",
        "再见 谢谢您一直看到这里",
        "你一点会拥有那段属于你的爱情 (ง •_•)ง"
      ],
      zIndex:-1,
      time:''
    };
  },
  methods: {},
  beforeDestroy() {
    clearInterval(this.time);
    cancelAnimationFrame(this.animationId);
  },
  created() {},
  filters: {
    daytotal(time) {
      let iTime = new Date().getTime() - new Date(time).getTime();
      return parseInt(iTime / (1000 * 60 * 60 * 24));
    }
  },
  mounted() {
    
this.time = setInterval(() => {
  this.zIndex ++
  if(this.zIndex >= this.ziArr.length){
    clearInterval(this.time);
  }
}, 10000);
    let that = this;
    //两个canvas   画树的canvas
    var tree = document.getElementById("tree");
    tree.width = window.innerWidth;
    tree.height = window.innerHeight;
    var tCxt = tree.getContext("2d");
    // 画花的canvas
    var flower = document.getElementById("flower");
    flower.width = window.innerWidth;
    flower.height = window.innerHeight;
    var cxt = flower.getContext("2d");

    var flowerList = []; //樱花列表
    var rootTop = tCxt.canvas.height - 20; //树起点
    var flowerColor = "rgba(255,192,203,.3)"; //花色
    var flowerColorDeep = "rgba(241,158,194,.5)"; //花色深
    var treeColor2 = "rgba(255,192,203,.5)"; //树枝颜色
    var treeColor = "#FFF"; //树干颜色
    var fallList = []; //飘落樱花列表
    var g = 0.0008; //重力加速度
    var gWind = 0.001; //风力加速度
    var limitSpeedY = 0.7; //速度上限
    // var limitSpeedX = 1 ;//速度上限

    cxt.shadowColor = "#FFF";
    cxt.shadowBlur = 10;
    // 映入图片
    let img = new Image();
    img.src = that.$http.defaults.baseURL+"/upload_files/2020-2-17/qiuhun.png";
    img.onload = () => {
      tCxt.beginPath();
      tCxt.drawImage(img, tree.width / 2 + 10, rootTop - 60, 60, 60);
    };

    function drawTree(x, y, deg, step, type) {
      var deg1 = step % 2 == 0 ? 0.1 : -0.1;
      var x1 = x + Math.cos(deg + deg1) * (step + 4) * 1; //以步长来判断枝干长度 x轴偏移大一些
      var y1 = y + Math.sin(deg + deg1) * (step - 1) * 1; //以步长来判断枝干长度 Y轴压缩一些
      tCxt.beginPath();
      tCxt.lineWidth = step / 3;
      tCxt.moveTo(x, y);
      tCxt.lineTo(x1, y1);
      tCxt.strokeStyle = step > 5 ? treeColor : treeColor2; //细纸条都换成花的颜色
      tCxt.stroke();
      if (step > 10) {
        //树干相交的位置有间隙，以一个圆填充
        tCxt.fillStyle = treeColor;
        tCxt.arc(x, y, step / 6, 0, Math.PI * 2);
        tCxt.fill();
      }
      if (step < 3 || (step < 23 && Math.random() > 0.1)) {
        //末梢位置 画花瓣
        var color = [flowerColorDeep, flowerColor, flowerColor][
          Math.round(Math.random() + 0.2)
        ];
        var r = 2 + Math.random() * 2;
        tCxt.fillStyle = color;
        tCxt.arc(x1 + Math.random() * 3, y1 + Math.random() * 3, r, 0, Math.PI);
        tCxt.fill();
        flowerList.push({
          x: x,
          y: y,
          sx: Math.random() - 0.5,
          sy: 0,
          color: color,
          r: r,
          deg: deg
        }); //保存下画樱花的位置
      }
      step--;
      if (step > 0) {
        drawTree(x1, y1, deg, step, type);
        if (step % 3 == 1 && step > 0 && step < 30)
          drawTree(
            x1,
            y1,
            deg + 0.2 + 0.3 * Math.random(),
            Math.round(step / 1.13)
          ); //右分叉
        if (step % 3 == 0 && step > 0 && step < 30)
          drawTree(
            x1,
            y1,
            deg - 0.2 - 0.3 * Math.random(),
            Math.round(step / 1.13)
          ); //左分叉
      }
    }

    drawTree(tree.width / 2, rootTop, -Math.PI / 2, 25, 1); //执行

    var len = flowerList.length;

    function step() {
      if (Math.random() > 0.3)
        fallList.push(flowerList[Math.floor(Math.random() * len)]); //随机取出一个，花瓣复制到飘落花瓣的列表中

      cxt.clearRect(0, 0, tree.width, tree.height);
      for (var i = 0; i < fallList.length; i++) {
        if (fallList[i].sy < limitSpeedY) fallList[i].sy += g;
        fallList[i].sx += gWind;
        fallList[i].x += fallList[i].sx;
        fallList[i].y += fallList[i].sy;
        if (fallList[i].y > rootTop) {
          //飘到树根的花瓣移除
          fallList.splice(i, 1);
          i--;
          continue;
        }
        cxt.beginPath();
        cxt.fillStyle = fallList[i].color;
        fallList[i].deg += fallList[i].sx * 0.05; //跟速度相关的旋转花瓣
        cxt.arc(
          fallList[i].x,
          fallList[i].y,
          fallList[i].r,
          fallList[i].deg,
          fallList[i].deg + Math.PI * 1.3
        );
        cxt.fill();
      }
      that.animationId = requestAnimationFrame(step);
    }
    // requestAnimationFrame(step);
    step();
  },
  watch: {
    allnavs: {
      handler(allnavs) {
        // 判断是否允许访问
        if (allnavs.navs && allnavs.navs.split(",")[3] != 1) {
          return this.$router.push("/");
        }
      },
      // 深度监听
      deep: true
    }
  }
};
</script>

<style>
.Partner canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.Partner .rightimg,
.Partner .leftgongao {
  position: absolute;
  top: 0;
  background-image: url("http://192.168.0.104:8888/upload_files/2020-2-17/gonggao.png");
  background-repeat: no-repeat;
}
/* 图片摇摆的动画 */
@keyframes yb {
  0% {
    transform: rotate(3deg);
    transform-origin: top center;
  }
  50% {
    transform: rotate(-3deg);
    transform-origin: top center;
  }
  100% {
    transform: rotate(3deg);
    transform-origin: top center;
  }
}
.Partner .rightimg {
  animation: yb 5s ease-in-out infinite;
}
.Partner .rightimg img {
  width: 78%;
  position: absolute;
  left: 50%;
  bottom: 52%;
  transform: translate(-50%, 80%); /*2d移动*/
  border-radius: 15px;
}
.Partner .leftgongao > div {
  width: 92%;
  height: 19%;
  position: absolute;
  left: 0;
  padding-left: 8px;
}
.Partner .leftgongao > div:first-child::before {
  display: inline-block;
  content: "";
  height: 100%;
  vertical-align: middle;
}
.Partner .leftgongao > div:first-child {
  top: 23%;
  text-align: center;
  font: 0.5em;
}
.Partner .leftgongao > div:last-child {
  height: 28%;
  bottom: 6%;
}
.Partner .leftgongao > div:last-child div {
  text-align: center;
  font-size: 0.8em;
  font-weight: bold;
}
.Partner #audio {
  opacity: 0.1;
  position: absolute;
  z-index: 99999;
  top: -12%;
  left: 40%;
  opacity: 0;
}
.Partner .zimu {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%); /*2d移动*/
  /* 动画 */
  animation: zm 4s ease-in-out,out 4s ease-in-out 7s;
  letter-spacing: 4px;
  font-size: 15px;
  font-family: 'Microsoft JhengHei';
  /* background-color: red; */
}
@keyframes out {
  0% {
    transform:translate(-50%, -50%) scale(1);
  }
  100% {
   transform:translate(-50%, -50%) scale(0);
  }
}
@keyframes zm {
  0% {
    transform:translate(-50%, -50%) scale(5);
    color: transparent;
    text-shadow: #fff 0 0 10px;
    opacity:0;
  }
  100% {
    transform:translate(-50%, -50%) scale(1);
    color: transparent;
    text-shadow: #fff 0 0 0px;
    opacity:1;
  }
}
.Partner .zimunone{
  display:none;
}
</style>